import axios from 'axios';

export default function (keywords,type) {
  const require = new Promise((resolve, reject) => {
    /*axios.get('http://47.94.16.170:3000/search',{
      params:{
        keywords: keywords,
        type: type
      },
    }).then((data)=> {
      resolve(data);
    });*/
    axios.get('/soso/fcgi-bin/search_for_qq_cp',{
      params:{
        w: keywords,
        format: 'json',
        inCharset: 'utf8',
        outCharset: 'utf-8',
        notice: 0,
        catZhida: 1,
        p: 1,
        perpage: 20,
        n: 20,
        g_tk: 5381,
        platform: 'h5',
        needNewCode: 1,
        zhidaqu: 1,
        t: 0,
        flag: 1,
        ie: 'utf-8',
        sem: 1,
        aggr: 0,
        remoteplace: 'txt.mqq.all'
      },
    }).then((data)=> {
      resolve(data.data.song.list);
    });
  });
  return require;
}



