<template>
  <div>
    <header>
      <i class="icon-back"
         @click="$router.push('/index/findMusic')">
         &#xe60c;
      </i>
      <input type="text" placeholder="搜索  #发现好歌#" class="search-box"
             @keyup.enter="searchMusic()"
             v-model="searchText">
      <div class="searxh-nav"
           @click="searchMusic()">
        <nav
          :class="{checked:searchType === 1}"
          @click="searchType = 1">单曲</nav>
        <nav
          :class="{checked:searchType === 100}"
          @click="searchType = 100">歌手</nav>
        <nav
          :class="{checked:searchType === 10}"
          @click="searchType = 10">专辑</nav>
        <nav
          :class="{checked:searchType === 1000}"
          @click="searchType = 1000">歌单</nav>
        <nav
          :class="{checked:searchType === 1002}"
          @click="searchType = 1002">用户</nav>
      </div>
    </header>
    <div class="content">
      <ul v-show="searchType === 1">
        <li v-for="(item, i) in content" class="songs">
          <div class="name">{{item.songname}}</div>
          <div class="artist">{{item.singer[0].name}}</div>
        </li>
      </ul>
      <ul v-show="searchType === 100">
        <li v-for="(item, i) in content.artists" class="artists">
          <img :src="item.img1v1Url">
          <div class="name">{{item.name}}<span v-show="item.alia!==undefined"> ({{item.alias[0]}})</span></div>
        </li>
      </ul>
      <ul v-show="searchType === 10">
        <li v-for="(item, i) in content.albums" class="albums artists">
          <img :src="item.picUrl">
          <div>
            <div class="name">{{item.name}}</div>
            <div>{{item.artist.name}}</div>
          </div>
        </li>
      </ul>
      <ul v-show="searchType === 1000">
        <li v-for="(item, i) in content.playlists" class="play-lists artists">
          <img :src="item.coverImgUrl">
          <div>
            <div class="name">{{item.name}}</div>
            <div>{{item.creator.nickname}}</div>
          </div>
        </li>
      </ul>

      <ul v-show="searchType === 1002">
        <li v-for="(item, i) in content.userprofiles" class="users artists">
          <img :src="item.avatarUrl">
          <div>
            <div class="name">{{item.nickname}}</div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  import search from '@/api/search';
  export default {
    name: "music-search",
    data(){
      return{
        searchText: '',
        searchType: 1,
        content: []
      }
    },
    mounted(){
    },
    methods:{
      searchMusic(){
        this.content = [];
        let that = this;
        search(this.searchText).then(function (data) {
          console.log(data);
          that.content = data;
        })
      }
    }
  }
</script>

<style scoped>
  header{
    display: flex;
    flex-wrap: wrap;
    height: 4rem;
    background-color: #c62f2f;
  }
  .icon-back{
    font-size: 1.3rem;
    color: #fff;
    text-align: center;
    width: 1.9rem;
    height: 2rem;
    margin-right: .6rem;
    display: inline-block;
    transform: rotate(90deg);
  }
  .search-box{
    box-sizing: border-box;
    color: #fff;
    height: 2rem !important;
    width: 15.5rem;
    border: 0;
    text-indent: 5px;
    background-color: #c62f2f;
    border-bottom: 1px solid #ffffff;
  }
  input::-webkit-input-placeholder{
    color: #cbcbcb;
  }
  .name{
    font-size: 14px;
  }
  .searxh-nav{
    color: #fff;
    width: 100%;
    display: flex;
    justify-content: space-around;
    font-size: 14px;
  }
  .searxh-nav nav{
    width: 2rem;
    text-align: center;
    box-sizing: border-box;
  }
  .checked{
    border-bottom: 2px solid #fff;
  }
  .content{
    width: 96%;
    margin: 0 auto;
  }
  .content .songs{
    border-bottom: 1px solid #d9d9d9;
    padding: .5rem;
    font-size: .7rem;
  }
  .artists{
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: center;
    margin: .3rem 0;
  }
  .artists img{
    width: 3rem;
    margin-right: .4rem;
  }
  .artists>div{
    flex-grow: 1;
    height: 3rem;
    flex-direction: column;
    justify-content: center;
    display: flex;
    border-bottom: 1px solid #d9d9d9;
  }
  li>div:last-of-type{
    font-size: .6rem;
  }
</style>



